<template>
  <div class="home">
    <div>
      <label class="labels">Current account</label>
      <select v-model="currentAccount">
          <option v-for="account in accounts" :value="account">
            {{ account.id }}
          </option>
      </select>
    </div>
    <div class="transaction-data">
      <h2>Transaction data</h2>
      <div>
        <label class="labels">Amount</label>
        <input v-model="transaction.amount">
      </div>
      <div>
        <label class="labels">Reason</label>
        <input v-model="transaction.reason"/>
      </div>
      <div>
        <button v-on:click="saveTransaction">
              Save transaction
            </button>
      </div>
    </div>
    <div class="transaction-list">
      <h2>Transaction list</h2>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Home',
  data () {
    return {
      transaction: {},
      accounts: [],
      currentAccount: null,
      allTransactions: []
    }
  },
  beforeCreate() {
    axios.get('get-bankAccounts')
    .then((response) => {
        this.accounts = response.data
        this.currentAccount = this.accounts[0]
    })
  },
  methods: {
    saveTransaction: function(event) {
      this.transaction.bankAccount = this.currentAccount
      axios.post('save-transaction', this.transaction)
      .then((response) => {
          if (response.data.type == 'REJECTED'){
            alert('Transaction was rejected')
          } else {
            alert('Transaction was accounted')
            this.transaction = {}
          }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



